* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.App .menu {
  height: 10vh;
  padding: 0rem 2rem;
  background: #E76D83;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.App .menu .logo {
  color: #f6e4e4;
  font-family: "Poppins", sans-serif;
  text-decoration: none;
  font-size: 1.3rem;
}

.App .menu ul {
  list-style: none;
  display: -webkit-flex;
  display: flex;
  margin-left: 1.25rem;
}

.App .menu .nav-link {
  font-family: "Poppins", sans-serif;
  color: #f6e4e4;
  text-decoration: none;
}

.App .menu .nav-link:hover {
  text-decoration: underline;
}

.App .menu li:nth-child(n+2) {
  margin-left: 0.5rem;
}

.App .banner {
  margin: 0 auto;
  margin-top: 1.5rem;
  width: 80%;
  max-width: 1200px;
  border: solid 5px #393D3F;
  color: #393D3F;
  text-align: center;
  padding: 0.5rem 0 4rem 0;
  font-family: "Poppins", sans-serif;
}

.App .banner p {
  font-size: 1.2rem;
}
.Search {
    margin-top: 1.5rem;
}
.SearchBox {
    margin: 0 auto;
    width: 80%;
    max-width: 1200px;
    border: solid 3px #393D3F;
    color: #393D3F;
    font-family: "Poppins", sans-serif;
    padding: 1rem;
}

.SearchBox input {
    display: block;
    margin: 0 auto;
    margin-top: 0.75rem;
    width: 100%;
    border: 2px solid #393D3F;
    padding: 0.25rem;
}

.SearchBox button {
    display: block;
    margin-left: auto;
    margin-top: 0.5rem;
    background: none;
    padding: 0.25rem 0.5rem;
    cursor: pointer;
}
.ResultsBox {
    margin: 0 auto;
    margin-top: 1.25rem;
    width: 80%;
    max-width: 1200px;
    border: solid 3px #393D3F;
    color: #393D3F;
    font-family: "Poppins", sans-serif;
    padding: 1rem;
}

.ResultsBox h2 {
    font-size: 1.2rem;
}
.BookResult {
    width: 100%;
    margin: 0 auto;
    border: 2px solid #393D3F;
    padding: 1rem 1.5rem;
}

.BookResult .top-half {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.BookResult .top-half-left p {
    font-size: 0.8rem;
}

.BookResult .top-half-right button {
    background: none;
    padding: 0.25rem 0.5rem;
    cursor: pointer;
}

.BookResult .top-half-right a {
    color: #393D3F;
    text-decoration: none;
}

.BookResult .top-half-right button:nth-child(n+2) {
    margin-left: 0.5rem;
}

.BookResult .bottom-half {
    display: -webkit-flex;
    display: flex;
    margin-top: 0.5rem;
}

.BookResult .bottom-half img {
    display: block;
    width: 100px;
    height: auto;
}

.BookResult .bottom-half p {
    font-size: 0.8rem;
    margin-left: 1rem;
}
